.header {
    padding: 32px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    max-width: 1376px;
    margin: 0 auto;

    .title {
        font-size: 24px;
        line-height: 28px;
        font-weight: 600;
    }

    .button {
        display: flex;
        gap: 15px;
        align-items: center;

        span {
            display: flex;
            align-items: center;
            font-size: 16px;
            line-height: 20px;
            color: #41522E;
        }
    }
}